const startBtn  = document.getElementById("startBtn");
const video = document.getElementById("preview");

let stream;
let recorder;
let videoFile;

const handleDownload = () => {
    const a = document.createElement("a");
    a.href = videoFile;
    a.download = "MyRecording.webm";
    document.body.appendChild(a);
    a.click();
}

const handleStop = () => {
  startBtn.innerText = "Download Recording";
  startBtn.removeEventListener("click", handleStop);
  startBtn.addEventListener("click", handleDownload);
  recorder.stop();
}

const handleStart = () => {
    startBtn.innerText = "Stop Recording";
    startBtn.removeEventListener("click", handleStart);
    startBtn.addEventListener("click", handleStop);
    
    recorder = new MediaRecorder(stream, {mineType: "video/webm"});
    // video.stop() ì‹¤í–‰ë˜ì—ˆì„ ë•Œ ondataavailable í•¨ìˆ˜ê°€ ìˆ˜í–‰ë¨
    recorder.ondataavailable = (e) => {
        console.log(e.data);
        // URL.createObjectURL: ë¸Œë¼ìš°ì €ì˜ ë©”ëª¨ë¦¬ ìƒì— ì €ìž¥í•˜ê³  ë¸Œë¼ìš°ì €ê°€ ê·¸ íŒŒì¼ì— ì ‘ê·¼í•  ìˆ˜ ìžˆëŠ” urlì„ ì œê³µ
        videoFile = URL.createObjectURL(e.data);
        video.srcObject = null;
        video.src = videoFile;
        video.loop = true;
        video.play();
    }
    // recording start
    recorder.start();
}

const init = async () => {
  stream = await navigator.mediaDevices.getUserMedia({
    audio: true,
    video: true,
  });
  video.srcObject = stream;
  video.play();
}

init();

startBtn.addEventListener("click", handleStart);